import { Icon, IconSizes, TextAndIconColors } from '@monetize-tech/ui-kit';
import classNames from 'classnames';
import React, { useEffect, useRef, useState } from 'react';
import { ReactComponent as ArrowIcon } from '../../assets/icons/arrowDown.svg';
import LanguageRow from './LanguageRow';
import styles from './LanguageSelect.module.scss';
import { LanguageSelectProps } from './LanguageSelect.types';
import { ELanguageCodes, langDataByCode } from '../../assets/language';

const LanguageSelect: React.FC<LanguageSelectProps> = ({ currentLang, onChange, list }: LanguageSelectProps) => {
  const wrapperRef = useRef<HTMLDivElement>(null);
  const [isActive, setIsActive] = useState<boolean>(false);

  useEffect(() => {
    const handleClickOutside = (event: Event) => {
      if (wrapperRef.current && !wrapperRef.current.contains(event.target as Node)) {
        setIsActive(false);
      }
    };
    if (isActive) document.addEventListener('mousedown', handleClickOutside);
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [wrapperRef, isActive]);
  const handleLanguageSelect = (langCode: ELanguageCodes) => {
    onChange(langCode);
    setIsActive(false);
  };

  const arrowIconCn = classNames(styles.arrowIcon, {
    [styles.arrowIcon_rotated]: isActive,
  });
  const languageSelectCn = classNames(styles.select, {
    [styles.select_active]: isActive,
  });

  const arrowIconColor = isActive ? TextAndIconColors.white : TextAndIconColors.labelsMenuSecondary;
  return (
    <div className={styles.wrapper} ref={wrapperRef}>
      <div className={languageSelectCn} onClick={() => setIsActive(!isActive)}>
        <Icon IconComponent={langDataByCode[currentLang]?.icon} size={IconSizes.l} />
        <Icon IconComponent={ArrowIcon} className={arrowIconCn} size={IconSizes.l} color={arrowIconColor} />
      </div>

      {isActive && (
        <div className={styles.languageList}>
          {list.map(({ languageTag }) => (
            <LanguageRow
              key={languageTag}
              tag={languageTag}
              onLanguageSelect={() => handleLanguageSelect(languageTag)}
              isCurrent={languageTag === currentLang}
            />
          ))}
        </div>
      )}
    </div>
  );
};

export default LanguageSelect;
