import React from 'react';
import { Icon, IconSizes, Text, TextAndIconColors, TextVariants } from '@monetize-tech/ui-kit';
import styles from './LanguageSelect.module.scss';
import { ILanguageIconProps } from './LanguageSelect.types';
import { getKcLanguageTagLabel } from 'keycloakify';
import { langDataByCode } from '../../assets/language';

const LanguageRow: React.FC<ILanguageIconProps> = ({ tag, onLanguageSelect, isCurrent }: ILanguageIconProps) => {
  const langIcon = langDataByCode[tag].icon;
  const textColor = isCurrent ? TextAndIconColors.white : TextAndIconColors.labelsMenuSecondary;

  return (
    <div className={styles.languageRow} onClick={onLanguageSelect}>
      <Icon IconComponent={langIcon} size={IconSizes.l} className={styles.languageRowIcon} />
      <Text text={getKcLanguageTagLabel(tag)} color={textColor} variant={TextVariants.body3} />
    </div>
  );
};

export default React.memo(LanguageRow);
