import { KcContextBase } from 'keycloakify';
import { useCallback, useEffect, useMemo, useState } from 'react';
import {
  DEFAULT_LANGUAGE,
  ELanguageCodes,
  langDataByCode,
  SUPPORTED_LANGUAGES,
  kcLanguages,
  LangTypes,
} from '../assets/language';
import { ILangContext } from '../context/LangContext';
import Storage, { LOCAL_STORAGE } from '../utils/storage';
import { updateDomLang } from '../utils/documentHtml';

const httpForLangChange = async (url: string | undefined) => {
  if (url) await fetch(url, { method: 'GET', headers: { 'Content-Type': 'application/json' } });
};

const getSupported = (kcLocale: KcContextBase['locale']): ILangContext['supported'] => {
  let result: ILangContext['supported'] = [];
  if (kcLocale?.supported?.length) {
    result = kcLocale.supported.filter((item) =>
      SUPPORTED_LANGUAGES.includes(item.languageTag as ELanguageCodes),
    ) as ILangContext['supported'];
  }
  return result;
};

const STORAGE_LOCALE_KEY = 'kc-fy_locale';
const storage = new Storage(LOCAL_STORAGE);
type CurrentType = { label: string; tag: ELanguageCodes };
const getCurrent = (kcLocale: KcContextBase['locale'], getBest: boolean): CurrentType => {
  const locale = storage.getItem(STORAGE_LOCALE_KEY);
  if (locale && SUPPORTED_LANGUAGES.includes(locale)) {
    return { tag: locale, label: langDataByCode[locale as ELanguageCodes].label };
  }

  const kcTag: LangTypes = kcLocale?.current
    ? kcLanguages.find((item) => item.label === kcLocale?.current)!.languageTag
    : 'en';
  let tag: ELanguageCodes = SUPPORTED_LANGUAGES.includes(kcTag as ELanguageCodes)
    ? (kcTag as ELanguageCodes)
    : DEFAULT_LANGUAGE;
  if (getBest && typeof navigator !== 'undefined') {
    const iso2LanguageLike = navigator.language.split('-')[0].toLowerCase();
    if (SUPPORTED_LANGUAGES.includes(iso2LanguageLike as ELanguageCodes)) {
      tag = iso2LanguageLike as ELanguageCodes;
    }
  }
  storage.setItem(STORAGE_LOCALE_KEY, tag);
  updateDomLang(tag.toString());

  return { label: langDataByCode[tag].label, tag };
};

const useLanguage = (internationalization: boolean | undefined, locale: KcContextBase['locale']): ILangContext => {
  const [base, setBase] = useState<CurrentType>(() => getCurrent(locale, true));
  const [current, setCurrent] = useState<CurrentType>(base);
  const supported: ILangContext['supported'] = useMemo(
    () => (internationalization ? getSupported(locale) : []),
    [locale, internationalization],
  );

  useEffect(() => {
    if (!internationalization) {
      return;
    }
    if (base.label !== current.label) {
      setBase(current);
      storage.setItem(STORAGE_LOCALE_KEY, current.tag);
      httpForLangChange(supported.find(({ languageTag }) => languageTag === current.tag)?.url);
      updateDomLang(current.tag.toString());
    }
  }, [internationalization, supported, current, base]);
  const onChangeLanguage = useCallback(
    (languageTag: ELanguageCodes) => {
      if (internationalization) setCurrent({ tag: languageTag, label: langDataByCode[languageTag].label });
    },
    [internationalization],
  );

  return {
    current: current.label,
    currentLanguageTag: current.tag,
    supported,
    onChangeLanguage,
  };
};

export default useLanguage;
