import React, { useCallback, useState } from 'react';
import Template from '../../modules/Template';
import { KcContextBase } from 'keycloakify';
import useMessage from '../../hooks/message.hook';
import { Button, ButtonTypes, Input, Text, TextVariants } from '@monetize-tech/ui-kit';
import styles from '../../assets/styles/pages.module.scss';

type LoginOauth2DeviceVerifyUserCodeProps = { kcContext: KcContextBase.LoginOauth2DeviceVerifyUserCode };

const LoginOauth2DeviceVerifyUserCode: React.FC<LoginOauth2DeviceVerifyUserCodeProps> = ({
  kcContext,
}: LoginOauth2DeviceVerifyUserCodeProps) => {
  const { url } = kcContext;

  const { msg } = useMessage();

  const [deviceUserCode, setDeviceUserCode] = useState<string>('');
  const changeDeviceUserCode = useCallback((newValue: string) => setDeviceUserCode(newValue), [setDeviceUserCode]);

  const headerNode = <Text variant={TextVariants.head3}>{msg('oauth2DeviceVerificationTitle')}</Text>;
  const formNode = (
    <div className={styles.wrapper}>
      <form id='kc-user-verify-device-user-code-form' action={url.oauth2DeviceVerificationAction} method='post'>
        <div className={styles.list}>
          <Input
            name='device_user_code'
            type='text'
            onChange={changeDeviceUserCode}
            value={deviceUserCode}
            placeholder={msg('verifyOAuth2DeviceUserCode')}
          />
        </div>
        <div className={styles.list}>
          <Button text={msg('doSubmit')} type={ButtonTypes.submit} fluid className={styles.button} />
        </div>
      </form>
    </div>
  );

  return <Template {...{ kcContext }} headerNode={headerNode} formNode={formNode} />;
};

export default React.memo(LoginOauth2DeviceVerifyUserCode);
