import { Icon } from '@monetize-tech/ui-kit';
import classNames from 'classnames';
import React, { FC } from 'react';
import { ReactComponent as LogoAdmitad } from '../../assets/icons/admitad.svg';
import styles from './Header.module.scss';
import { IHeaderProps } from './Header.types';

const Header: FC<IHeaderProps> = (props) => {
  const { endContent, className, onItemClick } = props;
  const headerClassName = classNames(styles.header, className);

  return (
    <header className={headerClassName}>
      <div className={styles.logoContainer}>
        <Icon IconComponent={LogoAdmitad} width={130} height={34} />
      </div>
      <div onClick={onItemClick} className={styles.emptySpace} />
      <div className={styles.endContent}>{endContent}</div>
    </header>
  );
};

export default React.memo(Header);
