import React from 'react';
import Template from '../../modules/Template';
import type { KcContextBase } from 'keycloakify';
import useMessage from '../../hooks/message.hook';
import { ELinkTarget, Link, Text, TextVariants } from '@monetize-tech/ui-kit';
import styles from './LoginVerifyEmail.module.scss';

const LoginVerifyEmail = ({ kcContext }: { kcContext: KcContextBase.LoginVerifyEmail }) => {
  const { url } = kcContext;

  const { msg } = useMessage();

  const headerNode = <Text variant={TextVariants.head3}>{msg('emailVerifyTitle')}</Text>;
  const formNode = (
    <div className={styles.wrapper}>
      <Text variant={TextVariants.body2}>{msg('emailVerifyInstruction1')}</Text>
      <Text variant={TextVariants.body2}>
        {msg('emailVerifyInstruction2')}
        <Link href={url.loginAction} target={ELinkTarget.self} underline={false}>
          {' ' + msg('doClickHere') + ' '}
        </Link>
        {msg('emailVerifyInstruction3')}
      </Text>
    </div>
  );

  return <Template {...{ kcContext }} displayMessage={false} headerNode={headerNode} formNode={formNode} />;
};

export default React.memo(LoginVerifyEmail);
