import React, { useEffect, FC, ComponentClass, useCallback } from 'react';
import type { KcContextBase, Attribute } from 'keycloakify';
import { useFormValidationSlice } from 'keycloakify';
import InputPassword from '../../components/InputPassword/InputPassword';
import { EInputThemes, Input, TextVariants, Text } from '@monetize-tech/ui-kit';
import useMessage from '../../hooks/message.hook';
import styles from './UserProfileFormFields.module.scss';

const cleaningDisplayName = (str: string): string => {
  return str.replaceAll('{', '').replaceAll('}', '').replaceAll('$', '');
};

type ReactComponent<Props extends Record<string, unknown>> = ((props: Props) => ReturnType<FC>) | ComponentClass<Props>;
type UserProfileFormFieldsProps = { kcContext: KcContextBase.RegisterUserProfile } & Partial<
  Record<'BeforeField' | 'AfterField', ReactComponent<{ attribute: Attribute }>>
> & {
    onIsFormSubmittableValueChange: (isFormSubmittable: boolean) => void;
  };
const UserProfileFormFields = ({ kcContext, onIsFormSubmittableValueChange }: UserProfileFormFieldsProps) => {
  const { msg } = useMessage();
  const {
    formValidationState: { fieldStateByAttributeName, isFormSubmittable },
    formValidationReducer,
    attributesWithPassword,
  } = useFormValidationSlice({
    kcContext,
  });
  useEffect(() => {
    onIsFormSubmittableValueChange(isFormSubmittable);
  }, [isFormSubmittable, onIsFormSubmittableValueChange]);

  let currentGroup = '';
  const onChange = useCallback(
    (name: string, newValue: string) =>
      formValidationReducer({
        action: 'update value',
        name,
        newValue: newValue,
      }),
    [formValidationReducer],
  );
  const onBlur = useCallback(
    (event: React.FocusEvent<HTMLInputElement>) =>
      formValidationReducer({
        action: 'focus lost',
        name: event.target.name,
      }),
    [formValidationReducer],
  );

  const fieldsList = attributesWithPassword.map((attribute, i) => {
    const { group = '', groupDisplayHeader = '', groupDisplayDescription = '', displayName, readOnly } = attribute;
    const { value, displayableErrors } = fieldStateByAttributeName[attribute.name];

    return (
      <React.Fragment key={i}>
        {group !== currentGroup && (currentGroup = group) !== '' && (
          <div className={styles.headers}>
            <Text variant={TextVariants.header5}>{groupDisplayHeader}</Text>
            {groupDisplayDescription !== '' && <Text variant={TextVariants.body2}>{groupDisplayDescription}</Text>}
          </div>
        )}
        {attribute.name.includes('password') ? (
          <InputPassword
            name={attribute.name}
            type='text'
            placeholder={msg(cleaningDisplayName(displayName || 'password') as 'password' | 'passwordConfirm')}
            autoComplete
            value={value}
            onChange={(newValue) => onChange(attribute.name, newValue)}
            errorMessage={displayableErrors
              .reduce((prev, item) => `${prev} ${item.errorMessageStr}`, '')
              .replace(/\s+/g, ' ')
              .trim()}
            theme={displayableErrors.length > 0 ? EInputThemes.error : EInputThemes.base}
            disabled={readOnly}
            onBlur={onBlur}
          />
        ) : (
          <Input
            name={attribute.name}
            type='text'
            placeholder={msg(
              cleaningDisplayName(displayName || 'firstName') as 'username' | 'firstName' | 'lastName' | 'email',
            )}
            autoComplete
            value={value}
            onChange={(newValue) => onChange(attribute.name, newValue)}
            errorMessage={displayableErrors
              .reduce((prev, item) => `${prev} ${item.errorMessageStr}`, '')
              .replace(/\s+/g, ' ')
              .trim()}
            theme={displayableErrors.length > 0 ? EInputThemes.error : EInputThemes.base}
            disabled={readOnly}
            onBlur={onBlur}
          />
        )}
      </React.Fragment>
    );
  });

  return <div className={styles.list}>{fieldsList}</div>;
};

export default React.memo(UserProfileFormFields);
