import React from 'react';
import Template from '../../modules/Template';
import useMessage from '../../hooks/message.hook';
import { ELinkTarget, Link, Text, TextVariants } from '@monetize-tech/ui-kit';
import { KcContextBase } from 'keycloakify';
import styles from '../../assets/styles/pages.module.scss';

type LoginPageExpiredProps = { kcContext: KcContextBase.LoginPageExpired };

const LoginPageExpired: React.FC<LoginPageExpiredProps> = ({ kcContext }: LoginPageExpiredProps) => {
  const { url } = kcContext;

  const { msg } = useMessage();

  const headerNode = <Text variant={TextVariants.head3}>{msg('pageExpiredTitle')}</Text>;
  const formNode = (
    <div className={styles.wrapper}>
      <div id='instruction1' className={styles.list}>
        <Text variant={TextVariants.body2}>
          {msg('pageExpiredMsg1')}
          <Link href={url.loginRestartFlowUrl} target={ELinkTarget.self} underline={false}>
            {' ' + msg('doClickHere') + '.'}
          </Link>
        </Text>
        <Text variant={TextVariants.body2}>
          {msg('pageExpiredMsg2')}
          <Link href={url.loginAction} target={ELinkTarget.self} underline={false}>
            {' ' + msg('doClickHere') + '.'}
          </Link>
        </Text>
      </div>
    </div>
  );
  return <Template {...{ kcContext }} headerNode={headerNode} formNode={formNode} />;
};

export default React.memo(LoginPageExpired);
