import React from 'react';
import Template from '../../modules/Template';
import type { KcContextBase } from 'keycloakify';
import { assert } from 'keycloakify';
import useMessage from '../../hooks/message.hook';
import { ELinkTarget, Link, Text, TextVariants } from '@monetize-tech/ui-kit';
import styles from '../../assets/styles/pages.module.scss';
import classNames from 'classnames';

const cleaningHeader = (str: string | undefined): string => {
  return str ? str.replaceAll('<', '').replaceAll('>', '') : '';
};

const Info = ({ kcContext }: { kcContext: KcContextBase.Info }) => {
  const { messageHeader, message, requiredActions, skipLink, pageRedirectUri, actionUri, client } = kcContext;

  const { msg } = useMessage();
  assert(message !== undefined);

  const headerNode = <Text variant={TextVariants.head3}>{cleaningHeader(messageHeader) || message.summary}</Text>;
  const formNode = (
    <div id='kc-info-message' className={styles.wrapper}>
      <div className={styles.list}>
        <Text variant={TextVariants.body2}>{message.summary}</Text>
        {requiredActions !== undefined && (
          <Text variant={TextVariants.caption}>
            {requiredActions.map((requiredAction) => msg(`requiredAction.${requiredAction}` as const)).join(', ')}
          </Text>
        )}
      </div>
      {!skipLink && pageRedirectUri !== undefined ? (
        <div className={classNames(styles.list, { [styles.center]: true })}>
          <Link href={pageRedirectUri} target={ELinkTarget.self} underline={false}>
            {msg('backToApplication')}
          </Link>
        </div>
      ) : actionUri !== undefined ? (
        <div className={classNames(styles.list, { [styles.center]: true })}>
          <Link href={actionUri} target={ELinkTarget.self} underline={false}>
            {msg('proceedWithAction')}
          </Link>
        </div>
      ) : (
        client.baseUrl !== undefined && (
          <div className={classNames(styles.list, { [styles.center]: true })}>
            <Link href={client.baseUrl} target={ELinkTarget.self} underline={false}>
              {msg('backToApplication')}
            </Link>
          </div>
        )
      )}
    </div>
  );

  return <Template {...{ kcContext }} displayMessage={false} headerNode={headerNode} formNode={formNode} />;
};

export default React.memo(Info);
