import React from 'react';
import { ReactComponent as CopyIcon } from '../../assets/icons/copy.svg';
import { Icon, TextAndIconColors, IconSizes } from '@monetize-tech/ui-kit';
import styles from './Copy.module.scss';
import { autoCopy } from '../../utils/documentHtml';

type CopyProps = { content: string | null };

const Copy: React.FC<CopyProps> = ({ content }: CopyProps) => {
  return (
    <div className={styles.wrapper} onClick={() => autoCopy({ content: content })}>
      <Icon IconComponent={CopyIcon} size={IconSizes.s} color={TextAndIconColors.black} />
    </div>
  );
};

export default Copy;