import React from 'react';
import { KcContextBase } from 'keycloakify';
import Template from '../../modules/Template';
import { Button, ButtonTypes, ButtonVariant, Text, TextVariants } from '@monetize-tech/ui-kit';
import useMessage from '../../hooks/message.hook';
import styles from '../../assets/styles/pages.module.scss';

type DeleteAccountConfirmProps = { kcContext: KcContextBase.DeleteAccountConfirm };

const DeleteAccountConfirm: React.FC<DeleteAccountConfirmProps> = ({ kcContext }: DeleteAccountConfirmProps) => {
  // eslint-disable-next-line @typescript-eslint/naming-convention
  const { url, triggered_from_aia } = kcContext;

  const { msg } = useMessage();

  const headerNode = <Text variant={TextVariants.head3}>{msg('deleteAccountConfirm')}</Text>;
  const formNode = (
    <div className={styles.wrapper}>
      <form action={url.loginAction} method='post'>
        <div className={styles.list}>
          <Text variant={TextVariants.body2}>{msg('deletingImplies')}</Text>
          <ul className={styles.htmlList}>
            <li>
              <Text variant={TextVariants.body3}>{msg('loggingOutImmediately')}</Text>
            </li>
            <li>
              <Text variant={TextVariants.body3}>{msg('errasingData')}</Text>
            </li>
          </ul>
          <Text variant={TextVariants.body2}>{msg('finalDeletionConfirmation')}</Text>
        </div>
        <div className={styles.list}>
          <Button text={msg('doConfirmDelete')} type={ButtonTypes.submit} fluid className={styles.button} />
          {triggered_from_aia && (
            <Button
              name='cancel-aia'
              value='true'
              text={msg('doCancel')}
              type={ButtonTypes.submit}
              fluid
              variant={ButtonVariant.outlined}
              className={styles.button}
            />
          )}
        </div>
      </form>
    </div>
  );
  const messageNode = {
    type: 'warning',
    node: msg('irreversibleAction'),
  };
  return <Template {...{ kcContext }} headerNode={headerNode} formNode={formNode} messageNode={messageNode} />;
};

export default React.memo(DeleteAccountConfirm);
