import React, { useEffect } from 'react';
import { getKcContext } from 'keycloakify';
import PageList from './PageList';
import './kcMessagesExtension';
import LangContext from '../../context/LangContext';
import useLanguage from '../../hooks/language.hook';
import mockData from '../../mocks';
import { ModeTypes } from '../../App.constants';
// import { KcContextBase } from 'keycloakify';

interface PageProps {
  template?: string;
  mode: ModeTypes;
}
const Page: React.FC<PageProps> = ({ template, mode }: PageProps) => {
  const { kcContext } = getKcContext(mockData(template));
  useEffect(() => {
    if (kcContext !== undefined) console.log(kcContext);
  }, [kcContext]);

  const { current, currentLanguageTag, supported, onChangeLanguage } = useLanguage(
    kcContext?.realm.internationalizationEnabled,
    kcContext?.locale,
  );

  if (kcContext === undefined) {
    // throw new Error();
    return <div>Error: Keycloak context not found.</div>;
  }

  return (
    <LangContext.Provider value={{ current, currentLanguageTag, supported, onChangeLanguage }}>
      <PageList kcContext={kcContext} mode={mode} />
    </LangContext.Provider>
  );
};

export default Page;
