import React, { useCallback, useState } from 'react';
import { KcContextBase, KcProps } from 'keycloakify';
import useMessage from '../../hooks/message.hook';
import {
  Button,
  ButtonTypes, ButtonVariant,
  Checkbox,
  EInputThemes,
  Text,
  TextAndIconColors,
  TextVariants,
} from '@monetize-tech/ui-kit';
import Template from '../../modules/Template';
import styles from '../../assets/styles/pages.module.scss';
import InputPassword from '../../components/InputPassword';

type LoginUpdatePasswordProps = { kcContext: KcContextBase.LoginUpdatePassword };

const LoginUpdatePassword: React.FC<LoginUpdatePasswordProps> = ({ kcContext }: LoginUpdatePasswordProps) => {
  const { url, messagesPerField, isAppInitiatedAction, username } = kcContext;

  const { msg } = useMessage();

  const [passwordNew, setPasswordNew] = useState('');
  const changePasswordNew = useCallback((newValue: string) => setPasswordNew(newValue), [setPasswordNew]);
  const [passwordConfirm, setPasswordConfirm] = useState('');
  const changePasswordConfirm = useCallback((newValue: string) => setPasswordConfirm(newValue), [setPasswordConfirm]);
  const [logout, setLogout] = useState<boolean>(isAppInitiatedAction);
  const changeLogout = useCallback((newValue: boolean) => setLogout(newValue), [setLogout]);
  const getInputTheme = useCallback(
    (fieldName: string) => (messagesPerField.existsError(fieldName) ? EInputThemes.error : EInputThemes.base),
    [messagesPerField],
  );

  const headerNode = <Text variant={TextVariants.head3}>{msg('updatePasswordTitle')}</Text>;
  const formNode = (
    <div className={styles.wrapper}>
      <form id='kc-passwd-update-form' action={url.loginAction} method='post'>
        <input
          type='text'
          id='username'
          name='username'
          value={username}
          autoComplete='username'
          readOnly
          style={{ display: 'none' }}
        />
        <input
          type='password'
          id='password'
          name='password'
          autoComplete='current-password'
          style={{ display: 'none' }}
        />
        <div className={styles.list}>
          <InputPassword
            name='password-new'
            type='text'
            onChange={changePasswordNew}
            value={passwordNew}
            placeholder={msg('passwordNew')}
            autoComplete={true}
            theme={getInputTheme('password-new')}
          />
          <InputPassword
            name='password-confirm'
            type='text'
            onChange={changePasswordConfirm}
            value={passwordConfirm}
            placeholder={msg('passwordConfirm')}
            autoComplete={true}
            theme={getInputTheme('password-confirm')}
          />
          {isAppInitiatedAction && (
            <Checkbox
              name='logout-sessions'
              checked={logout}
              onChange={changeLogout}
              // value={logout ? 'on' : 'off'}
            >
              <Text variant={TextVariants.body5} color={TextAndIconColors.labelsTertiary}>
                {msg('logoutOtherSessions')}
              </Text>
            </Checkbox>
          )}
        </div>
        <div className={styles.list}>
          {isAppInitiatedAction ? (
            <>
              <Button text={msg('doSubmit')} type={ButtonTypes.submit} fluid className={styles.button} />
              <Button
                text={msg('doCancel')}
                type={ButtonTypes.submit}
                fluid
                variant={ButtonVariant.outlined}
                className={styles.button}
                name='cancel-aia'
                value='true'
              />
            </>
          ) : (
            <Button text={msg('doSubmit')} type={ButtonTypes.submit} fluid className={styles.button} />
          )}
        </div>
      </form>
    </div>
  );
  return <Template {...{ kcContext }} headerNode={headerNode} formNode={formNode} />;
};

export default React.memo(LoginUpdatePassword);
