import React, { forwardRef, useState } from 'react';
import { Icon, IconSizes, Input, TextAndIconColors } from '@monetize-tech/ui-kit';
import { ReactComponent as EyeClosed } from '../../assets/icons/eyeClosed.svg';
import { ReactComponent as EyeOpened } from '../../assets/icons/eyeOpened.svg';
import { InputPasswordProps } from './InputPassword.types';

const InputPassword: React.FC<InputPasswordProps> = forwardRef(
  ({ hideEye, value, errorMessage, ...props }: InputPasswordProps, ref) => {
    const [passwordShown, setPasswordShown] = useState(false);

    const togglePasswordVisibility = (): void => {
      setPasswordShown(!passwordShown);
    };

    const EyeIconComponent = (
      <div onClick={togglePasswordVisibility}>
        <Icon
          IconComponent={!passwordShown ? EyeClosed : EyeOpened}
          color={TextAndIconColors.interfaceSecondary}
          size={IconSizes.l}
        />
      </div>
    );

    return (
      <Input
        {...props}
        endContent={!hideEye ? (value ? EyeIconComponent : undefined) : undefined}
        value={value}
        ref={ref}
        errorMessage={errorMessage}
        type={passwordShown ? 'text' : 'password'}
      />
    );
  },
);

export default React.memo(InputPassword);
