import React, { useContext } from 'react';
import type { ReactNode } from 'react';
import type { KcContextBase } from 'keycloakify';
import { useConstCallback } from 'powerhooks/useConstCallback';
import classNames from 'classnames';
import mStyles from './Template.module.scss';
import Header from '../../components/Header';
import { LanguageSelect } from '../../components/LanguageSelect';
import {
  ELinkTarget,
  ETooltipThemes,
  Icon,
  Link,
  Text,
  TextAndIconColors,
  TextVariants,
  Tooltip,
  TooltipShowEvents,
} from '@monetize-tech/ui-kit';
import { ReactComponent as Logo } from '../../assets/icons/logo.svg';
import LangContext from '../../context/LangContext';
import useMessage from '../../hooks/message.hook';
import { ReactComponent as ExternalLinkAlt } from '../../assets/icons/externalLinkAlt.svg';
import { openLinkSelf } from '../../utils/url';

export type TemplateProps = {
  displayInfo?: boolean;
  displayMessage?: boolean;
  displayRequiredFields?: boolean;
  displayWide?: boolean;
  showAnotherWayIfPresent?: boolean;
  headerNode: ReactNode;
  showUsernameNode?: ReactNode;
  formNode: ReactNode;
  infoNode?: ReactNode;
  messageNode?: { type: string; node: ReactNode | string } | null;
} & { kcContext: KcContextBase };

const Template = (props: TemplateProps) => {
  const {
    displayMessage = true,
    displayWide = false,
    showAnotherWayIfPresent = true,
    headerNode,
    showUsernameNode = null,
    formNode,
    kcContext,
    messageNode = null,
  } = props;

  const { msg } = useMessage();
  const { realm, auth, url, message, isAppInitiatedAction } = kcContext;
  const { onChangeLanguage, currentLanguageTag, supported } = useContext(LangContext);

  const onTryAnotherWayClick = useConstCallback(
    () => (document.forms['kc-select-try-another-way-form' as never].submit(), false),
  );
  const languageContent = realm.internationalizationEnabled && supported.length > 1 && (
    <LanguageSelect currentLang={currentLanguageTag} onChange={onChangeLanguage} list={supported} />
  );

  const cardClass = classNames(mStyles.card, {
    [mStyles.account]: displayWide,
    [mStyles.message]: displayMessage && message !== undefined && (message.type !== 'warning' || !isAppInitiatedAction),
  });
  const messageClass = classNames(mStyles.message, {
    [mStyles.success]: message?.type === 'success' || messageNode?.type === 'success',
    [mStyles.warning]: message?.type === 'warning' || messageNode?.type === 'warning',
    [mStyles.error]: message?.type === 'error' || messageNode?.type === 'error',
    [mStyles.info]: message?.type === 'info' || messageNode?.type === 'info',
  });

  return (
    <div className={mStyles.wrapper}>
      <Header endContent={languageContent} />
      <div className={mStyles.main}>
        {displayMessage && message !== undefined && !messageNode && (
          <div className={messageClass}>
            <Text variant={TextVariants.body3} color={TextAndIconColors.white}>
              {message.summary}
            </Text>
          </div>
        )}
        {messageNode && (
          <div className={messageClass}>
            <Text variant={TextVariants.body3} color={TextAndIconColors.white}>
              {messageNode.node}
            </Text>
          </div>
        )}
        <div className={cardClass}>
          <div className={mStyles.header}>
            {!(auth !== undefined && auth.showUsername && !auth.showResetCredentials) ? (
              headerNode
            ) : (
              <>
                {showUsernameNode}
                <div className={mStyles.usernameNode}>
                  <Text variant={TextVariants.header5}>{auth?.attemptedUsername}</Text>
                  <Tooltip
                    content={<Text>{msg('restartLoginTooltip')}</Text>}
                    showOn={TooltipShowEvents.hover}
                    theme={ETooltipThemes.interfaceOnSurface}
                    shadow
                  >
                    <div onClick={() => openLinkSelf(url.loginRestartFlowUrl)} className={mStyles.link}>
                      <Icon IconComponent={ExternalLinkAlt} width={20} height={20} color={TextAndIconColors.black} />
                    </div>
                  </Tooltip>
                </div>
              </>
            )}
          </div>
          <div id='kc-content'>
            <div id='kc-content-wrapper'>
              {formNode}
              {auth !== undefined && auth.showTryAnotherWayLink && showAnotherWayIfPresent && (
                <form
                  id='kc-select-try-another-way-form'
                  action={url.loginAction}
                  method='post'
                  className={mStyles.marginTop}
                >
                  <input type='hidden' name='tryAnotherWay' value='on' />
                  <Link href={'#'} target={ELinkTarget.self} underline={false} onClick={onTryAnotherWayClick}>
                    {msg('doTryAnotherWay')}
                  </Link>
                </form>
              )}
            </div>
          </div>
        </div>
      </div>
      <div className={mStyles.footer}>
        <Icon IconComponent={Logo} color={TextAndIconColors.white} height={50} width={50} className={mStyles.logo} />
      </div>
    </div>
  );
};

export default React.memo(Template);
