import React, { useCallback } from 'react';
import Template from '../../modules/Template';
import type { KcContextBase } from 'keycloakify';
import useMessage from '../../hooks/message.hook';
import { Button, ButtonTypes, ButtonVariant, EInputThemes, Input, Text, TextVariants } from '@monetize-tech/ui-kit';
import styles from '../../assets/styles/pages.module.scss';
import { useUpdateState } from '../../hooks/helper.hook';

const LoginUpdateProfile = ({ kcContext }: { kcContext: KcContextBase.LoginUpdateProfile }) => {
  const { url, user, messagesPerField, isAppInitiatedAction } = kcContext;

  const { msg } = useMessage();

  const [username, setUsername] = useUpdateState(user.username ?? '');
  const changeUsername = useCallback((newValue: string) => setUsername(newValue), [setUsername]);
  const [email, setEmail] = useUpdateState(user.email ?? '');
  const changeEmail = useCallback((newValue: string) => setEmail(newValue), [setEmail]);
  const [firstName, setFirstName] = useUpdateState(user.firstName ?? '');
  const changeFirstName = useCallback((newValue: string) => setFirstName(newValue), [setFirstName]);
  const [lastName, setLastName] = useUpdateState(user.lastName ?? '');
  const changeLastName = useCallback((newValue: string) => setLastName(newValue), [setLastName]);
  const getInputTheme = useCallback(
    (fieldName: string) => (messagesPerField.existsError(fieldName) ? EInputThemes.error : EInputThemes.base),
    [messagesPerField],
  );

  const headerNode = <Text variant={TextVariants.head3}>{msg('loginProfileTitle')}</Text>;
  const formNode = (
    <div className={styles.wrapper}>
      <form id='kc-update-profile-form' action={url.loginAction} method='post'>
        <div className={styles.list}>
          {user.editUsernameAllowed && (
            <Input
              name='username'
              type='text'
              onChange={changeUsername}
              value={username}
              placeholder={msg('username')}
              theme={getInputTheme('username')}
              errorMessage={messagesPerField.existsError('username') ? messagesPerField.get('username') : ''}
            />
          )}
          <Input
            name='email'
            type='text'
            onChange={changeEmail}
            value={email}
            placeholder={msg('email')}
            theme={getInputTheme('email')}
            errorMessage={messagesPerField.existsError('email') ? messagesPerField.get('email') : ''}
          />
          <Input
            name='firstName'
            type='text'
            onChange={changeFirstName}
            value={firstName}
            placeholder={msg('firstName')}
            theme={getInputTheme('firstName')}
            errorMessage={messagesPerField.existsError('firstName') ? messagesPerField.get('firstName') : ''}
          />
          <Input
            name='lastName'
            type='text'
            onChange={changeLastName}
            value={lastName}
            placeholder={msg('lastName')}
            theme={getInputTheme('lastName')}
            errorMessage={messagesPerField.existsError('lastName') ? messagesPerField.get('lastName') : ''}
          />
        </div>
        <div className={styles.list}>
          {isAppInitiatedAction ? (
            <>
              <Button text={msg('doSubmit')} type={ButtonTypes.submit} fluid className={styles.button} />
              <Button
                text={msg('doCancel')}
                type={ButtonTypes.submit}
                fluid
                variant={ButtonVariant.outlined}
                className={styles.button}
                name='cancel-aia'
                value='true'
              />
            </>
          ) : (
            <Button text={msg('doSubmit')} type={ButtonTypes.submit} fluid className={styles.button} />
          )}
        </div>
      </form>
    </div>
  );

  return <Template {...{ kcContext }} headerNode={headerNode} formNode={formNode} displayMessage={false} />;
};

export default React.memo(LoginUpdateProfile);
