import React, { useEffect } from 'react';
import { KcContextBase } from 'keycloakify';
import useMessage from '../../hooks/message.hook';
import Template from '../../modules/Template';
import { Text, TextVariants } from '@monetize-tech/ui-kit';
import styles from '../../assets/styles/pages.module.scss';
import { formSubmit } from './SamlPostForm.functions';
import { MODE_PRIMARY_THEME_BUILD, MODE_SECONDARY_THEME_BUILD } from '../../App.constants';

const FORM_ID = 'saml-post-form';

type SamlPostFormProps = { kcContext: KcContextBase.SamlPostForm };

const SamlPostForm: React.FC<SamlPostFormProps> = ({ kcContext }: SamlPostFormProps) => {
  const { samlPost } = kcContext;

  const { msg } = useMessage();

  useEffect(() => {
    if (
      process.env.REACT_APP_MODE === MODE_PRIMARY_THEME_BUILD ||
      process.env.REACT_APP_MODE === MODE_SECONDARY_THEME_BUILD
    ) {
      formSubmit(FORM_ID);
    }
  }, []);

  const headerNode = <Text variant={TextVariants.head3}>{msg('saml.post-form.title')}</Text>;
  const formNode = (
    <div className={styles.wrapper}>
      <div className={styles.list}>
        <Text variant={TextVariants.body2}>{msg('saml.post-form.message')}</Text>
      </div>
      <form id={FORM_ID} name='saml-post-binding' method='post' action={samlPost.url}>
        {samlPost.SAMLRequest && <input type='hidden' name='SAMLRequest' value={samlPost.SAMLRequest} />}
        {samlPost.SAMLResponse && <input type='hidden' name='SAMLResponse' value={samlPost.SAMLResponse} />}
        {samlPost.relayState && <input type='hidden' name='relayState' value={samlPost.relayState} />}
      </form>
    </div>
  );

  return <Template {...{ kcContext }} headerNode={headerNode} formNode={formNode} />;
};

export default SamlPostForm;
