import React from 'react';
import Template from '../../modules/Template';
import { KcContextBase } from 'keycloakify';
import useMessage from '../../hooks/message.hook';
import { Icon, Text, TextAndIconColors, TextVariants } from '@monetize-tech/ui-kit';
import styles from './SelectAuthenticator.module.scss';
import { ReactComponent as ArrowRight } from '../../assets/icons/arrowRight.svg';

const FORM_ID = 'kc-select-credential-form';
const INPUT_ID = 'authexec-hidden-input';
const formSubmit = (authExecId: string) => {
  const input = document.querySelector(`#${INPUT_ID}`) as HTMLInputElement;
  if (input) input.value = authExecId;
  const form = document.querySelector(`#${FORM_ID}`) as HTMLFormElement;
  if (form) form.submit();
};

type SelectAuthenticatorProps = { kcContext: KcContextBase.SelectAuthenticator };

const SelectAuthenticator: React.FC<SelectAuthenticatorProps> = ({ kcContext }: SelectAuthenticatorProps) => {
  const { url, auth } = kcContext;

  const { msg } = useMessage();

  const headerNode = <Text variant={TextVariants.head3}>{msg('loginChooseAuthenticator')}</Text>;
  const formNode = (
    <div className={styles.wrapper}>
      <form id={FORM_ID} action={url.loginAction} method='post'>
        <div className={styles.list}>
          {auth.authenticationSelections?.map((item, id) => (
            <div key={id} className={styles.methodItem} onClick={() => formSubmit(item.authExecId)}>
              <div className={styles.methodContent}>
                <Text variant={TextVariants.header6}>{item.displayName}</Text>
                <Text variant={TextVariants.body2}>{item.helpText}</Text>
              </div>
              <Icon IconComponent={ArrowRight} width={16} height={16} color={TextAndIconColors.black} />
            </div>
          ))}
        </div>
        <input type='hidden' id={INPUT_ID} name='authenticationExecution' />
      </form>
    </div>
  );
  return <Template {...{ kcContext }} headerNode={headerNode} formNode={formNode} />;
};

export default React.memo(SelectAuthenticator);
