import { createContext } from 'react';
import { DEFAULT_LANGUAGE, ELanguageCodes, langDataByCode } from '../assets/language';

export interface ILangContext {
  current: string;
  currentLanguageTag: ELanguageCodes;
  supported: {
    languageTag: ELanguageCodes;
    label: string;
    url: string;
  }[];
  onChangeLanguage(languageTag: ELanguageCodes): void;
}

const LangContext = createContext({
  current: langDataByCode[DEFAULT_LANGUAGE].label,
  currentLanguageTag: DEFAULT_LANGUAGE,
  supported: [],
  onChangeLanguage: () => {},
} as ILangContext);

export default LangContext;
