import React from 'react';
import type { KcContextBase } from 'keycloakify';
import Login from '../../pages/Login';
import Register from '../../pages/Register';
import RegisterUserProfile from '../../pages/RegisterUserProfile';
import Info from '../../pages/Info';
import Error from '../../pages/Error';
import LoginResetPassword from '../../pages/LoginResetPassword';
import LoginVerifyEmail from '../../pages/LoginVerifyEmail';
import Terms from '../../pages/Terms';
import LoginOtp from '../../pages/LoginOtp';
import LoginUpdateProfile from '../../pages/LoginUpdateProfile';
import LoginIdpLinkConfirm from '../../pages/LoginIdpLinkConfirm';
import LoginConfigTotp from '../../pages/LoginConfigTotp';
import SamlPostForm from '../../pages/SamlPostForm/SamlPostForm';
import { ModeTypes } from '../../App.constants';
import LoginUpdatePassword from '../../pages/LoginUpdatePassword/LoginUpdatePassword';
import Code from '../../pages/Code/Code';
import DeleteAccountConfirm from '../../pages/DeleteAccountConfirm/DeleteAccountConfirm';
import LoginIdpLinkEmail from '../../pages/LoginIdpLinkEmail/LoginIdpLinkEmail';
import LoginOauth2DeviceVerifyUserCode
  from '../../pages/LoginOauth2DeviceVerifyUserCode/LoginOauth2DeviceVerifyUserCode';
import LoginOauthGrant from '../../pages/LoginOauthGrant/LoginOauthGrant';
import LoginPageExpired from '../../pages/LoginPageExpired/LoginPageExpired';
import LoginPassword from '../../pages/LoginPassword/LoginPassword';
import LoginUsername from '../../pages/LoginUsername/LoginUsername';
import SelectAuthenticator from '../../pages/SelectAuthenticator/SelectAuthenticator';

const PageList = ({ kcContext, mode }: { kcContext: KcContextBase } & { mode: ModeTypes }) => {
  switch (kcContext.pageId) {
    case 'login.ftl':
      return <Login {...{ kcContext, mode }} />;
    case 'register.ftl':
      return <Register {...{ kcContext }} />;
    case 'register-user-profile.ftl':
      return <RegisterUserProfile {...{ kcContext }} />;
    case 'info.ftl':
      return <Info {...{ kcContext }} />;
    case 'error.ftl':
      return <Error {...{ kcContext }} />;
    case 'login-reset-password.ftl':
      return <LoginResetPassword {...{ kcContext }} />;
    case 'login-verify-email.ftl':
      return <LoginVerifyEmail {...{ kcContext }} />;
    case 'terms.ftl':
      return <Terms {...{ kcContext }} />;
    case 'login-otp.ftl':
      return <LoginOtp {...{ kcContext }} />;
    case 'login-update-profile.ftl':
      return <LoginUpdateProfile {...{ kcContext }} />;
    case 'login-idp-link-confirm.ftl':
      return <LoginIdpLinkConfirm {...{ kcContext }} />;
    case 'login-config-totp.ftl':
      return <LoginConfigTotp {...{ kcContext }} />;
    case 'saml-post-form.ftl':
      return <SamlPostForm {...{ kcContext }} />;
    case 'login-update-password.ftl':
      return <LoginUpdatePassword {...{ kcContext }} />;
    case 'code.ftl':
      return <Code {...{ kcContext }} />;
    case 'delete-account-confirm.ftl':
      return <DeleteAccountConfirm {...{ kcContext }} />;
    case 'login-idp-link-email.ftl':
      return <LoginIdpLinkEmail {...{ kcContext }} />;
    case 'login-oauth2-device-verify-user-code.ftl':
      return <LoginOauth2DeviceVerifyUserCode {...{ kcContext }} />;
    case 'login-oauth-grant.ftl':
      return <LoginOauthGrant {...{ kcContext }} />;
    case 'login-page-expired.ftl':
      return <LoginPageExpired {...{ kcContext }} />;
    case 'login-password.ftl':
      return <LoginPassword {...{ kcContext }} />;
    case 'login-username.ftl':
      return <LoginUsername {...{ kcContext }} />;
    case 'select-authenticator.ftl':
      return <SelectAuthenticator {...{ kcContext }} />;
    default:
      return null;
  }
};

export default React.memo(PageList);
