import { useContext, useMemo } from 'react';
import { MessageKey, kcMessages } from 'keycloakify';
import { customMessages, ELanguageCodes } from '../assets/language';
import LangContext from '../context/LangContext';

const msg = (key: MessageKey, tag: ELanguageCodes): string => {
  return kcMessages[tag as any as 'en'][key] || '';
};

const substitute = (text: string, replacers?: string[]): string => {
  let result: string = text;
  const places: string[] | undefined = text.match(/{\d+}/g)?.filter((item, id, arr) => id === arr.indexOf(item));
  if (places?.length) {
    places.forEach((item) => {
      const index: string = item.replace('{', '').replace('}', '');
      result =
        replacers?.length && replacers[Number.parseInt(index, 10)]
          ? result.replaceAll(item, replacers[Number.parseInt(index, 10)])
          : result.replaceAll(item, '');
    });
  }
  return result;
};

const useMessage = () => {
  const { currentLanguageTag } = useContext(LangContext);

  return useMemo(
    () => ({
      msg: (key: MessageKey, replacers?: string[]) =>
        substitute(customMessages[currentLanguageTag]?.[key] || msg(key, currentLanguageTag), replacers),
    }),
    [currentLanguageTag],
  );
};

export default useMessage;
