import React from 'react';
import Template from '../../modules/Template';
import { KcContextBase } from 'keycloakify';
import useMessage from '../../hooks/message.hook';
import { Button, ButtonTypes, ButtonVariant, Text, TextVariants } from '@monetize-tech/ui-kit';
import styles from '../../assets/styles/pages.module.scss';

type LoginOauthGrantProps = { kcContext: KcContextBase.LoginOauthGrant };

const LoginOauthGrant: React.FC<LoginOauthGrantProps> = ({ kcContext }: LoginOauthGrantProps) => {
  const { url, client, oauth } = kcContext;

  const { msg } = useMessage();

  const headerNode = client.name ? (
    <Text variant={TextVariants.head3}>{msg('oauthGrantTitle', [client.name])}</Text>
  ) : (
    <Text variant={TextVariants.head3}>{msg('oauthGrantTitle', [client.clientId])}</Text>
  );
  const formNode = (
    <div id='kc-oauth' className={styles.wrapper}>
      <div className={styles.list}>
        <Text variant={TextVariants.header6}>{msg('oauthGrantRequest')}</Text>
        {oauth.clientScopesRequested && (
          <ul className={styles.htmlList}>
            {oauth.clientScopesRequested.map((item, id) => (
              <li key={id}>
                <Text variant={TextVariants.body2}>{item.consentScreenText}</Text>
              </li>
            ))}
          </ul>
        )}
      </div>
      <form action={url.oauthAction} method='POST'>
        <input type='hidden' name='code' value={oauth.code} />
        <div className={styles.list}>
          <Button name='accept' text={msg('doYes')} type={ButtonTypes.submit} fluid className={styles.button} />
          <Button
            name='cancel'
            text={msg('doNo')}
            type={ButtonTypes.submit}
            variant={ButtonVariant.outlined}
            fluid
            className={styles.button}
          />
        </div>
      </form>
    </div>
  );

  return <Template {...{ kcContext }} headerNode={headerNode} formNode={formNode} />;
};

export default React.memo(LoginOauthGrant);
