import React, { useCallback, useState } from 'react';
import Template from '../../modules/Template';
import { KcContextBase } from 'keycloakify';
import useMessage from '../../hooks/message.hook';
import { Button, ButtonTypes, EInputThemes, ELinkTarget, Link, Text, TextVariants } from '@monetize-tech/ui-kit';
import styles from '../../assets/styles/pages.module.scss';
import { useConstCallback } from 'powerhooks/useConstCallback';
import InputPassword from '../../components/InputPassword';
import classNames from 'classnames';

type LoginPasswordProps = { kcContext: KcContextBase.LoginPassword };

const LoginPassword: React.FC<LoginPasswordProps> = ({ kcContext }: LoginPasswordProps) => {
  const { url, messagesPerField, realm } = kcContext;

  const { msg } = useMessage();

  const [isLoginButtonDisabled, setIsLoginButtonDisabled] = useState(false);
  const onSubmit = useConstCallback(() => (setIsLoginButtonDisabled(true), true));
  const [password, setPassword] = useState('');
  const changePassword = useCallback((newValue: string) => setPassword(newValue), [setPassword]);
  const getInputTheme = useCallback(
    (fieldName: string) => (messagesPerField.existsError(fieldName) ? EInputThemes.error : EInputThemes.base),
    [messagesPerField],
  );

  const headerNode = <Text variant={TextVariants.head3}>{msg('doLogIn')}</Text>;
  const formNode = (
    <div id='kc-form' className={styles.wrapper}>
      <form id='kc-form-login' onSubmit={onSubmit} action={url.loginAction} method='post'>
        <div className={styles.list}>
          <InputPassword
            name='password'
            type='text'
            onChange={changePassword}
            value={password}
            placeholder={msg('password')}
            autoComplete={true}
            theme={getInputTheme('password')}
            errorMessage={messagesPerField.existsError('password') ? messagesPerField.get('password') : ''}
          />
        </div>
        <div className={classNames(styles.list, { [styles.center]: true })}>
          {realm.resetPasswordAllowed && (
            <Link href={url.loginResetCredentialsUrl} target={ELinkTarget.self} underline={false}>
              {msg('doForgotPassword')}
            </Link>
          )}
        </div>
        <div className={styles.list}>
          <Button
            name='login'
            value={msg('doLogIn')}
            text={msg('doLogIn')}
            disabled={isLoginButtonDisabled}
            type={ButtonTypes.submit}
            fluid
            className={styles.button}
          />
        </div>
      </form>
    </div>
  );
  return <Template {...{ kcContext }} headerNode={headerNode} formNode={formNode} displayMessage={false} />;
};

export default React.memo(LoginPassword);
