import React, { useCallback, useState } from 'react';
import Template from '../../modules/Template';
import type { KcContextBase } from 'keycloakify';
import styles from '../../assets/styles/pages.module.scss';
import useMessage from '../../hooks/message.hook';
import { Button, ButtonTypes, EInputThemes, ELinkTarget, Input, Link, Text, TextVariants } from '@monetize-tech/ui-kit';
import { useUpdateState } from '../../hooks/helper.hook';
import InputPassword from '../../components/InputPassword';
import classNames from 'classnames';

type RegisterProps = { kcContext: KcContextBase.Register };

const Register: React.FC<RegisterProps> = ({ kcContext }: RegisterProps) => {
  const { url, messagesPerField, register, realm, passwordRequired, recaptchaRequired, recaptchaSiteKey } = kcContext;

  const { msg } = useMessage();

  const [firstName, setFirstName] = useUpdateState(register.formData.firstName ?? '');
  const changeFirstName = useCallback((newValue: string) => setFirstName(newValue), [setFirstName]);
  const [lastName, setLastName] = useUpdateState(register.formData.lastName ?? '');
  const changeLastName = useCallback((newValue: string) => setLastName(newValue), [setLastName]);
  const [email, setEmail] = useUpdateState(register.formData.email ?? '');
  const changeEmail = useCallback((newValue: string) => setEmail(newValue), [setEmail]);
  const [username, setUsername] = useUpdateState(register.formData.username ?? '');
  const changeUsername = useCallback((newValue: string) => setUsername(newValue), [setUsername]);
  const [password, setPassword] = useState('');
  const changePassword = useCallback((newValue: string) => setPassword(newValue), [setPassword]);
  const [passwordConfirm, setPasswordConfirm] = useState('');
  const changePasswordConfirm = useCallback((newValue: string) => setPasswordConfirm(newValue), [setPasswordConfirm]);

  const getInputTheme = useCallback(
    (fieldName: string) => (messagesPerField.existsError(fieldName) ? EInputThemes.error : EInputThemes.base),
    [messagesPerField],
  );

  const headerNode = <Text variant={TextVariants.head3}>{msg('registerTitle')}</Text>;
  const formNode = (
    <div className={styles.wrapper}>
      <form id='kc-register-form' action={url.registrationAction} method='post'>
        <div className={styles.list}>
          <Input
            name='firstName'
            type='text'
            onChange={changeFirstName}
            value={firstName}
            placeholder={msg('firstName')}
            theme={getInputTheme('firstName')}
          />
          <Input
            name='lastName'
            type='text'
            onChange={changeLastName}
            value={lastName}
            placeholder={msg('lastName')}
            theme={getInputTheme('lastName')}
          />
          <Input
            name='email'
            type='text'
            onChange={changeEmail}
            value={email}
            placeholder={msg('email')}
            theme={getInputTheme('email')}
          />
          {!realm.registrationEmailAsUsername && (
            <Input
              name='username'
              type='text'
              onChange={changeUsername}
              value={username}
              placeholder={msg('username')}
              theme={getInputTheme('username')}
            />
          )}
          {passwordRequired && (
            <>
              <InputPassword
                name='password'
                type='text'
                onChange={changePassword}
                value={password}
                placeholder={msg('password')}
                autoComplete={true}
                theme={getInputTheme('password')}
              />
              <InputPassword
                name='password-confirm'
                type='text'
                onChange={changePasswordConfirm}
                value={passwordConfirm}
                placeholder={msg('passwordConfirm')}
                theme={getInputTheme('password-confirm')}
              />
            </>
          )}
          {recaptchaRequired && (
            <div className='form-group'>
              <div className='col-xs-12 col-sm-12 col-md-12 col-lg-12'>
                <div className='g-recaptcha' data-size='compact' data-sitekey={recaptchaSiteKey}></div>
              </div>
            </div>
          )}
        </div>
        <div className={styles.list}>
          <Button text={msg('doRegister')} type={ButtonTypes.submit} fluid className={styles.button} />
        </div>
        <div className={classNames(styles.list, { [styles.center]: true })}>
          <Link href={url.loginUrl} target={ELinkTarget.self} underline={false}>
            {msg('backToLogin')}
          </Link>
        </div>
      </form>
    </div>
  );

  return <Template {...{ kcContext }} headerNode={headerNode} formNode={formNode} />;
};

export default React.memo(Register);
