export const autoCopy = ({ content }: { content: string | null }): boolean => {
  if (content) {
    const text: string = content.toString();

    const area = document.createElement('textarea');
    document.body.append(area);
    area.value = text;
    area.select();
    document.execCommand('copy');
    area.remove();
    return true;
  }
  return false;
};

export const updateDomLang = (language: string) => {
  const html = document.querySelectorAll('html')[0];
  html.setAttribute('lang', language);
};

export const insertScript = (script: string) => {
  const el = document.createElement('script');
  el.innerHTML = script;
  document.body.append(el);
};

export const stringToDomElements = (string: string): HTMLCollection => {
  const el = document.createElement('div');
  el.innerHTML = string;
  return el.children;
};

export const getDomFromString = (string: string): HTMLCollectionOf<HTMLDivElement> => {
  const htmlStart: number = string.indexOf('<div');
  const htmlEnd: number = string.lastIndexOf('</div>');
  const tags: string = string
    .slice(htmlStart, htmlEnd + 6)
    .replace(new RegExp('\\s+', 'g'), ' ')
    .replace(new RegExp('\\n', 'g'), '');
  return stringToDomElements(tags) as HTMLCollectionOf<HTMLDivElement>;
};
