import React, { useState } from 'react';
import Template from '../../modules/Template';
import type { KcContextBase } from 'keycloakify';
import UserProfileFormFields from './UserProfileFormFields';
import useMessage from '../../hooks/message.hook';
import { Button, ButtonTypes, ELinkTarget, Link, Text, TextVariants } from '@monetize-tech/ui-kit';
import styles from '../../assets/styles/pages.module.scss';
import classNames from 'classnames';

const RegisterUserProfile = ({ kcContext }: { kcContext: KcContextBase.RegisterUserProfile }) => {
  const { url, messagesPerField, recaptchaRequired, recaptchaSiteKey } = kcContext;

  const { msg } = useMessage();

  const [isFomSubmittable, setIsFomSubmittable] = useState(false);

  const headerNode = <Text variant={TextVariants.head3}>{msg('registerTitle')}</Text>;
  const formNode = (
    <div className={styles.wrapper}>
      <form id='kc-register-form' action={url.registrationAction} method='post'>
        <UserProfileFormFields kcContext={kcContext} onIsFormSubmittableValueChange={setIsFomSubmittable} />
        {recaptchaRequired && (
          <div className='form-group'>
            <div className='col-xs-12 col-sm-12 col-md-12 col-lg-12'>
              <div className='g-recaptcha' data-size='compact' data-sitekey={recaptchaSiteKey}></div>
            </div>
          </div>
        )}
        <div className={styles.list}>
          <Button
            text={msg('doRegister')}
            type={ButtonTypes.submit}
            fluid
            className={styles.button}
            disabled={!isFomSubmittable}
          />
        </div>
        <div className={classNames(styles.list, { [styles.center]: true })}>
          <Link href={url.loginUrl} target={ELinkTarget.self} underline={false}>
            {msg('backToLogin')}
          </Link>
        </div>
      </form>
    </div>
  );

  return (
    <Template
      {...{ kcContext }}
      displayMessage={messagesPerField.exists('global')}
      headerNode={headerNode}
      formNode={formNode}
    />
  );
};

export default React.memo(RegisterUserProfile);
