import React from 'react';
import Template from '../../modules/Template';
import { Input, Text, TextVariants } from '@monetize-tech/ui-kit';
import useMessage from '../../hooks/message.hook';
import { KcContextBase } from 'keycloakify';
import styles from '../../assets/styles/pages.module.scss';
import Copy from '../../components/Copy';

type CodeProps = { kcContext: KcContextBase.Code };

const Code: React.FC<CodeProps> = ({ kcContext }: CodeProps) => {
  const { code } = kcContext;

  const { msg } = useMessage();

  const headerNode = code.success ? (
    <Text variant={TextVariants.head3}>{msg('codeSuccessTitle')}</Text>
  ) : (
    <Text variant={TextVariants.head3}>{msg('codeErrorTitle')}</Text>
  );
  const formNode = (
    <div id='kc-code' className={styles.wrapper}>
      {code.success ? (
        <div className={styles.list}>
          <Text variant={TextVariants.body2}>{msg('copyCodeInstruction')}</Text>
          <Input type='text' value={code.code} autoComplete endContent={<Copy content={code.code || null} />} />
        </div>
      ) : (
        <div className={styles.list}>
          <Text variant={TextVariants.body2}>{code.error}</Text>
        </div>
      )}
    </div>
  );

  return <Template {...{ kcContext }} headerNode={headerNode} formNode={formNode} displayMessage={false} />;
};

export default React.memo(Code);
