import React, { useCallback, useState } from 'react';
import Template from '../../modules/Template';
import type { KcContextBase } from 'keycloakify';
import { useConstCallback } from 'powerhooks/useConstCallback';
import {
  Button,
  ButtonTypes,
  ButtonVariant,
  Checkbox,
  EInputThemes,
  ELinkTarget,
  ETooltipThemes,
  Input,
  Link,
  Text,
  TextAndIconColors,
  TextVariants,
  Tooltip,
  TooltipShowEvents,
} from '@monetize-tech/ui-kit';
import classNames from 'classnames';
import styles from './Login.module.scss';
import useMessage from '../../hooks/message.hook';
import { openLinkSelf } from '../../utils/url';
import { useUpdateState } from '../../hooks/helper.hook';
import InputPassword from '../../components/InputPassword';
import { MODE_SECONDARY_THEME_BUILD, MODE_SECONDARY_THEME_START, ModeTypes } from '../../App.constants';
import Social from '../../components/Social';

type LoginProps = { kcContext: KcContextBase.Login } & { mode: ModeTypes };

const Login: React.FC<LoginProps> = ({ kcContext, mode }: LoginProps) => {
  const { social, realm, url, usernameEditDisabled, login, auth, registrationDisabled, messagesPerField } = kcContext;

  const { msg } = useMessage();

  const [isLoginButtonDisabled, setIsLoginButtonDisabled] = useState(false);
  const onSubmit = useConstCallback(() => (setIsLoginButtonDisabled(true), true));
  const [username, setUsername] = useUpdateState(login.username ?? '');
  const changeUsername = useCallback((newValue: string) => setUsername(newValue), [setUsername]);
  const [password, setPassword] = useState('');
  const changePassword = useCallback((newValue: string) => setPassword(newValue), [setPassword]);
  const [rememberMe, setRememberMe] = useState<boolean>(!!login.rememberMe);
  const changeRememberMe = useCallback((newValue: boolean) => setRememberMe(newValue), [setRememberMe]);

  const placeholderUsername = msg(
    !realm.loginWithEmailAllowed ? 'username' : !realm.registrationEmailAsUsername ? 'usernameOrEmail' : 'email',
  );

  const getInputTheme = useCallback(
    () =>
      messagesPerField.existsError('username') || messagesPerField.existsError('password')
        ? EInputThemes.error
        : EInputThemes.base,
    [messagesPerField],
  );
  const headerNode = <Text variant={TextVariants.head3}>{msg('doLogIn')}</Text>;
  const formNode = (
    <div className={styles.wrapper}>
      {realm.password && (
        <>
          <form id='kc-form-login' onSubmit={onSubmit} action={url.loginAction} method='post'>
            <div className={styles.list}>
              {(mode === MODE_SECONDARY_THEME_BUILD || mode === MODE_SECONDARY_THEME_START) && (
                <Text
                  variant={TextVariants.header4}
                  color={TextAndIconColors.actionErrorPrimary}
                  style={{ textAlign: 'center' }}
                >
                  Это вторая тема!!
                </Text>
              )}
              <Input
                name='username'
                type='text'
                onChange={changeUsername}
                value={username}
                placeholder={placeholderUsername}
                autoComplete={true}
                theme={getInputTheme()}
                disabled={usernameEditDisabled}
              />
              <InputPassword
                name='password'
                type='text'
                onChange={changePassword}
                value={password}
                placeholder={msg('password')}
                autoComplete={true}
                theme={getInputTheme()}
              />
              {realm.rememberMe && !usernameEditDisabled && (
                <Checkbox name='rememberMe' onChange={changeRememberMe} checked={rememberMe}>
                  <Text variant={TextVariants.body5} color={TextAndIconColors.labelsTertiary}>
                    {msg('rememberMe')}
                  </Text>
                </Checkbox>
              )}
            </div>
            <div className={styles.list}>
              <input
                type='hidden'
                id='id-hidden-input'
                name='credentialId'
                {...(auth?.selectedCredential !== undefined
                  ? {
                      value: auth.selectedCredential,
                    }
                  : {})}
              />
              <Button
                name='login'
                text={msg('doLogIn')}
                disabled={isLoginButtonDisabled}
                type={ButtonTypes.submit}
                fluid
                className={styles.button}
              />
              {realm.registrationAllowed && !registrationDisabled && (
                <Button
                  text={msg('doRegister')}
                  disabled={isLoginButtonDisabled}
                  type={ButtonTypes.button}
                  fluid
                  variant={ButtonVariant.outlined}
                  onClick={() => openLinkSelf(url.registrationUrl)}
                  className={styles.button}
                />
              )}
            </div>
          </form>
          <div className={classNames(styles.list, { [styles.center]: true })}>
            {realm.resetPasswordAllowed && (
              <Link href={url.loginResetCredentialsUrl} target={ELinkTarget.self} underline={false}>
                {msg('doForgotPassword')}
              </Link>
            )}
          </div>
          {social.providers !== undefined && (
            <div className={styles.socialList}>
              {social.providers.map((item) => (
                <Tooltip
                  key={item.providerId}
                  content={<Text>{item.displayName}</Text>}
                  showOn={TooltipShowEvents.hover}
                  theme={ETooltipThemes.interfaceOnSurface}
                  shadow
                >
                  <div className={`${styles.socialItem}`} onClick={() => openLinkSelf(item.loginUrl)}>
                    <Social id={item.providerId} />
                  </div>
                </Tooltip>
              ))}
            </div>
          )}
        </>
      )}
    </div>
  );
  return (
    <Template
      {...{ kcContext }}
      displayWide={realm.password && social.providers !== undefined}
      headerNode={headerNode}
      formNode={formNode}
    />
  );
};

export default React.memo(Login);
