import React, { useCallback, useState } from 'react';
import Template from '../../modules/Template';
import { KcContextBase } from 'keycloakify';
import useMessage from '../../hooks/message.hook';
import {
  Button,
  ButtonTypes,
  ButtonVariant,
  Checkbox,
  EInputThemes,
  ETooltipThemes,
  Input,
  Text,
  TextAndIconColors,
  TextVariants,
  Tooltip,
  TooltipShowEvents,
} from '@monetize-tech/ui-kit';
import styles from './LoginUsername.module.scss';
import { useConstCallback } from 'powerhooks/useConstCallback';
import { useUpdateState } from '../../hooks/helper.hook';
import { openLinkSelf } from '../../utils/url';
import Social from '../../components/Social';

type LoginUsernameProps = { kcContext: KcContextBase.LoginUsername };

const LoginUsername: React.FC<LoginUsernameProps> = ({ kcContext }: LoginUsernameProps) => {
  const { url, realm, messagesPerField, login, usernameEditDisabled, social, registrationDisabled } = kcContext;

  const { msg } = useMessage();

  const [isLoginButtonDisabled, setIsLoginButtonDisabled] = useState(false);
  const onSubmit = useConstCallback(() => (setIsLoginButtonDisabled(true), true));
  const [username, setUsername] = useUpdateState(login.username ?? '');
  const changeUsername = useCallback((newValue: string) => setUsername(newValue), [setUsername]);
  const [rememberMe, setRememberMe] = useState<boolean>(!!login.rememberMe);
  const changeRememberMe = useCallback((newValue: boolean) => setRememberMe(newValue), [setRememberMe]);
  const getInputTheme = useCallback(
    (fieldName: string) => (messagesPerField.existsError(fieldName) ? EInputThemes.error : EInputThemes.base),
    [messagesPerField],
  );

  const placeholderUsername = msg(
    !realm.loginWithEmailAllowed ? 'username' : !realm.registrationEmailAsUsername ? 'usernameOrEmail' : 'email',
  );
  const headerNode = <Text variant={TextVariants.head3}>{msg('loginAccountTitle')}</Text>;
  const formNode = (
    <div id='kc-form' className={styles.wrapper}>
      {realm.password && (
        <>
          <form id='kc-form-login' onSubmit={onSubmit} action={url.loginAction} method='post'>
            <div className={styles.list}>
              <Input
                name='username'
                type='text'
                onChange={changeUsername}
                value={username}
                placeholder={placeholderUsername}
                autoComplete={true}
                theme={getInputTheme('username')}
                errorMessage={messagesPerField.existsError('username') ? messagesPerField.get('username') : ''}
                disabled={usernameEditDisabled}
              />
              {realm.rememberMe && !usernameEditDisabled && (
                <Checkbox name='rememberMe' onChange={changeRememberMe} checked={rememberMe}>
                  <Text variant={TextVariants.body5} color={TextAndIconColors.labelsTertiary}>
                    {msg('rememberMe')}
                  </Text>
                </Checkbox>
              )}
            </div>
            <div className={styles.list}>
              <Button
                name='login'
                text={msg('doLogIn')}
                disabled={isLoginButtonDisabled}
                type={ButtonTypes.submit}
                fluid
                className={styles.button}
              />
              {realm.registrationAllowed && !registrationDisabled && (
                <Button
                  text={msg('doRegister')}
                  disabled={isLoginButtonDisabled}
                  type={ButtonTypes.button}
                  fluid
                  variant={ButtonVariant.outlined}
                  onClick={() => openLinkSelf(url.registrationUrl)}
                  className={styles.button}
                />
              )}
            </div>
          </form>
          {social.providers !== undefined && (
            <div className={styles.socialList}>
              {social.providers.map((item) => (
                <Tooltip
                  key={item.providerId}
                  content={<Text>{item.displayName}</Text>}
                  showOn={TooltipShowEvents.hover}
                  theme={ETooltipThemes.interfaceOnSurface}
                  shadow
                >
                  <div className={`${styles.socialItem}`} onClick={() => openLinkSelf(item.loginUrl)}>
                    <Social id={item.providerId} />
                  </div>
                </Tooltip>
              ))}
            </div>
          )}
        </>
      )}
    </div>
  );
  return <Template {...{ kcContext }} headerNode={headerNode} formNode={formNode} displayMessage={false} />;
};

export default React.memo(LoginUsername);
