import React from 'react';
import { Icon, TextAndIconColors } from '@monetize-tech/ui-kit';
import { ReactComponent as Google } from '../../assets/icons/social/google.svg';
import { ReactComponent as Question } from '../../assets/icons/social/question.svg';
import { ReactComponent as GitHub } from '../../assets/icons/social/github.svg';
import { ReactComponent as Facebook } from '../../assets/icons/social/facebook.svg';
import { ReactComponent as Linkedin } from '../../assets/icons/social/linkedin.svg';
import { ReactComponent as Instagram } from '../../assets/icons/social/instagram.svg';
import { ReactComponent as Microsoft } from '../../assets/icons/social/microsoft.svg';
import { ReactComponent as BitBucket } from '../../assets/icons/social/bitbucket.svg';
import { ReactComponent as Twitter } from '../../assets/icons/social/twitter.svg';
import { ReactComponent as GitLab } from '../../assets/icons/social/gitlab.svg';
import { ReactComponent as PayPal } from '../../assets/icons/social/paypal.svg';
import { ReactComponent as StackOverflow } from '../../assets/icons/social/stack-overflow.svg';
import { ReactComponent as Yandex } from '../../assets/icons/social/yandex.svg';
import { ReactComponent as Vkontakte } from '../../assets/icons/social/vkontakte.svg';
// https://fontawesome.com/v5.15/icons?d=gallery&p=2&m=free

type SocialProps = {
  id: string;
};

const Social: React.FC<SocialProps> = ({ id }: SocialProps) => {
  switch (id) {
    case 'google':
      return <Icon IconComponent={Google} width={32} height={32} color={TextAndIconColors.black} />;
    case 'github':
      return <Icon IconComponent={GitHub} width={32} height={32} color={TextAndIconColors.black} />;
    case 'facebook':
      return <Icon IconComponent={Facebook} width={32} height={32} color={TextAndIconColors.black} />;
    case 'linkedin':
      return <Icon IconComponent={Linkedin} width={32} height={32} color={TextAndIconColors.black} />;
    case 'instagram':
      return <Icon IconComponent={Instagram} width={32} height={32} color={TextAndIconColors.black} />;
    case 'microsoft':
      return <Icon IconComponent={Microsoft} width={32} height={32} color={TextAndIconColors.black} />;
    case 'bitbucket':
      return <Icon IconComponent={BitBucket} width={32} height={32} color={TextAndIconColors.black} />;
    case 'twitter':
      return <Icon IconComponent={Twitter} width={32} height={32} color={TextAndIconColors.black} />;
    case 'gitlab':
      return <Icon IconComponent={GitLab} width={32} height={32} color={TextAndIconColors.black} />;
    case 'paypal':
      return <Icon IconComponent={PayPal} width={32} height={32} color={TextAndIconColors.black} />;
    case 'stack-overflow':
      return <Icon IconComponent={StackOverflow} width={32} height={32} color={TextAndIconColors.black} />;
    case 'yandex':
      return <Icon IconComponent={Yandex} width={32} height={32} color={TextAndIconColors.black} />;
    case 'vk':
      return <Icon IconComponent={Vkontakte} width={32} height={32} color={TextAndIconColors.black} />;
    default:
      return <Icon IconComponent={Question} width={32} height={32} color={TextAndIconColors.black} />;
  }
};

export default Social;
