import React, { useCallback } from 'react';
import Template from '../../modules/Template';
import type { KcContextBase } from 'keycloakify';
import styles from '../../assets/styles/pages.module.scss';
import useMessage from '../../hooks/message.hook';
import { Button, ButtonTypes, EInputThemes, ELinkTarget, Input, Link, Text, TextVariants } from '@monetize-tech/ui-kit';
import { useUpdateState } from '../../hooks/helper.hook';
import classNames from 'classnames';

const LoginResetPassword = ({ kcContext }: { kcContext: KcContextBase.LoginResetPassword }) => {
  const { url, realm, auth, messagesPerField } = kcContext;

  const { msg } = useMessage();

  const [username, setUsername] = useUpdateState<string>(
    auth !== undefined && auth.showUsername ? auth.attemptedUsername || '' : '',
  );
  const changeUsername = useCallback((newValue: string) => setUsername(newValue), [setUsername]);
  const getInputTheme = useCallback(
    (fieldName: string) => (messagesPerField.existsError(fieldName) ? EInputThemes.error : EInputThemes.base),
    [messagesPerField],
  );

  const placeholderUsername = !realm.loginWithEmailAllowed
    ? msg('username')
    : !realm.registrationEmailAsUsername
    ? msg('usernameOrEmail')
    : msg('email');
  const headerNode = <Text variant={TextVariants.head3}>{msg('emailForgotTitle')}</Text>;
  const formNode = (
    <div className={styles.wrapper}>
      <form id='kc-reset-password-form' action={url.loginAction} method='post'>
        <div className={styles.list}>
          <Text variant={TextVariants.body2}>{msg('emailInstruction')}</Text>
          <Input
            name='username'
            type='text'
            onChange={changeUsername}
            value={username}
            placeholder={placeholderUsername}
            theme={getInputTheme('username')}
          />
        </div>
        <div className={styles.list}>
          <Button text={msg('doSubmit')} type={ButtonTypes.submit} fluid className={styles.button} />
        </div>
        <div className={classNames(styles.list, { [styles.center]: true })}>
          <Link href={url.loginUrl} target={ELinkTarget.self} underline={false}>
            {msg('backToLogin')}
          </Link>
        </div>
      </form>
    </div>
  );

  return <Template {...{ kcContext }} headerNode={headerNode} formNode={formNode} />;
};

export default React.memo(LoginResetPassword);
