import React, { useCallback, useState } from 'react';
import { KcContextBase, MessageKey } from 'keycloakify';
import useMessage from '../../hooks/message.hook';
import Template from '../../modules/Template';
import {
  Text,
  TextAndIconColors,
  TextVariants,
  Link,
  ELinkTarget,
  Input,
  EInputThemes,
  ButtonTypes,
  Button, ButtonVariant,
} from '@monetize-tech/ui-kit';
import styles from './LoginConfigTotp.module.scss';

type LoginConfigTotpProps = { kcContext: KcContextBase.LoginConfigTotp };

const LoginConfigTotp: React.FC<LoginConfigTotpProps> = ({ kcContext }: LoginConfigTotpProps) => {
  const { totp, mode, url, messagesPerField, isAppInitiatedAction } = kcContext;

  const { msg } = useMessage();

  const getInputTheme = useCallback(
    (fieldName: string) => (messagesPerField.existsError(fieldName) ? EInputThemes.error : EInputThemes.base),
    [messagesPerField],
  );
  const [totpCode, setTotpCode] = useState<string>('');
  const changeTotpCode = useCallback((newValue: string) => setTotpCode(newValue), [setTotpCode]);
  const [userLabel, setUserLabel] = useState<string>('');
  const changeUserLabel = useCallback((newValue: string) => setUserLabel(newValue), [setUserLabel]);

  const headerNode = <Text variant={TextVariants.head3}>{msg('loginTotpTitle')}</Text>;
  const formNode = (
    <div className={styles.wrapper}>
      <ol className={styles.htmlList}>
        <li>
          <Text variant={TextVariants.body2}>{msg('loginTotpStep1')}</Text>
          <ul id='kc-totp-supported-apps'>
            {totp.policy.supportedApplications.map((item, id) => (
              <li key={id}>
                <Text variant={TextVariants.body2} color={TextAndIconColors.labelsTertiary}>
                  {item}
                </Text>
              </li>
            ))}
          </ul>
        </li>
        {mode && mode === 'manual' ? (
          <>
            <li>
              <Text variant={TextVariants.body2}>{msg('loginTotpManualStep2')}</Text>
              <br />
              <Text variant={TextVariants.body2} color={TextAndIconColors.labelsTertiary}>
                {totp.totpSecretEncoded}
              </Text>
              <br />
              <Link href={totp.qrUrl} target={ELinkTarget.self} underline={false}>
                {msg('loginTotpScanBarcode')}
              </Link>
            </li>
            <li>
              <Text variant={TextVariants.body2}>{msg('loginTotpManualStep3')}</Text>
              <ul>
                <li id='kc-totp-type'>
                  <Text variant={TextVariants.body4}>{msg('loginTotpType')}:</Text>
                  <Text variant={TextVariants.body4} color={TextAndIconColors.labelsTertiary} className={styles.margin}>
                    {msg(`loginTotp.${totp.policy.type}` as MessageKey)}
                  </Text>
                </li>
                <li id='kc-totp-algorithm'>
                  <Text variant={TextVariants.body4}>{msg('loginTotpAlgorithm')}:</Text>
                  <Text variant={TextVariants.body4} color={TextAndIconColors.labelsTertiary} className={styles.margin}>
                    {totp.policy.getAlgorithmKey()}
                  </Text>
                </li>
                <li id='kc-totp-digits'>
                  <Text variant={TextVariants.body4}>{msg('loginTotpDigits')}:</Text>
                  <Text variant={TextVariants.body4} color={TextAndIconColors.labelsTertiary} className={styles.margin}>
                    {totp.policy.digits}
                  </Text>
                </li>
                {totp.policy.type === 'totp' ? (
                  <li id='kc-totp-period'>
                    <Text variant={TextVariants.body4}>{msg('loginTotpInterval')}:</Text>
                    <Text
                      variant={TextVariants.body4}
                      color={TextAndIconColors.labelsTertiary}
                      className={styles.margin}
                    >
                      {totp.policy.period}
                    </Text>
                  </li>
                ) : (
                  <li id='kc-totp-counter'>
                    <Text variant={TextVariants.body4}>{msg('loginTotpCounter')}:</Text>
                    <Text
                      variant={TextVariants.body4}
                      color={TextAndIconColors.labelsTertiary}
                      className={styles.margin}
                    >
                      {totp.policy.initialCounter}
                    </Text>
                  </li>
                )}
              </ul>
            </li>
          </>
        ) : (
          <li>
            <Text variant={TextVariants.body2}>{msg('loginTotpStep2')}</Text>
            <div className={styles.center}>
              <img
                id='kc-totp-secret-qr-code'
                src={`data:image/png;base64, ${totp.totpSecretQrCode}`}
                alt='Figure: Barcode'
                className={styles.center}
              />
              <br />
              <Link href={totp.manualUrl} target={ELinkTarget.self} underline={false}>
                {msg('loginTotpUnableToScan')}
              </Link>
            </div>
          </li>
        )}
        <li>
          <Text variant={TextVariants.body2}>{msg('loginTotpStep3')}</Text>
          <br />
          <Text variant={TextVariants.body2}>{msg('loginTotpStep3DeviceName')}</Text>
        </li>
      </ol>
      <form action={url.loginAction} id='kc-totp-settings-form' method='post'>
        <div className={styles.list}>
          <Input
            name='totp'
            type='text'
            onChange={changeTotpCode}
            value={totpCode}
            placeholder={msg('authenticatorCode')}
            theme={getInputTheme('totp')}
            required
            errorMessage={messagesPerField.existsError('totp') ? messagesPerField.get('totp') : ''}
          />
          <input type='hidden' id='totpSecret' name='totpSecret' value={totp.totpSecret} />
          {mode && <input type='hidden' id='mode' name='mode' value={mode} />}

          <Input
            name='userLabel'
            type='text'
            onChange={changeUserLabel}
            value={userLabel}
            placeholder={msg('loginTotpDeviceName')}
            theme={getInputTheme('userLabel')}
            required={totp.otpCredentials.length > 0}
            errorMessage={messagesPerField.existsError('userLabel') ? messagesPerField.get('userLabel') : ''}
          />
        </div>
        <div className={styles.list}>
          {isAppInitiatedAction ? (
            <>
              <Button text={msg('doSubmit')} type={ButtonTypes.submit} fluid className={styles.button} />
              <Button
                text={msg('doCancel')}
                type={ButtonTypes.submit}
                fluid
                variant={ButtonVariant.outlined}
                className={styles.button}
                name='cancel-aia'
                value='true'
              />
            </>
          ) : (
            <Button text={msg('doSubmit')} type={ButtonTypes.submit} fluid className={styles.button} />
          )}
        </div>
      </form>
    </div>
  );

  return <Template {...{ kcContext }} headerNode={headerNode} formNode={formNode} displayMessage={false} />;
};

export default React.memo(LoginConfigTotp);
