type DeepPartial<T> = {
  [P in keyof T]?: DeepPartial<T[P]>;
};
const mockData = (
  template?: string,
): {
  mockPageId?: KcContextBase['pageId'];
  mockData?: DeepPartial<KcContextBase>[];
} => {
  switch (process.env.REACT_APP_MODE) {
    case MODE_PRIMARY_THEME_START:
    case MODE_SECONDARY_THEME_START:
      return {
        mockPageId: 'login.ftl',
        // mockData: [
        //   {
        //     pageId: 'login.ftl',
        //     auth: {
        //       showUsername: true,
        //       attemptedUsername: '123',
        //       showTryAnotherWayLink: true,
        //     },
        //   },
        // ],
      };
    case MODE_DEMO:
      switch (template) {
        case 'login':
          return {
            mockPageId: 'login.ftl',
            mockData: [
              {
                pageId: 'login.ftl',
                social: {
                  providers: providers,
                },
                auth: {
                  showTryAnotherWayLink: true,
                },
              },
            ],
          };
        case 'login-config-totp-2':
          return {
            mockPageId: 'login-config-totp.ftl',
            mockData: [
              {
                pageId: 'login-config-totp.ftl',
                mode: 'manual',
              },
            ],
          };
        case 'code-2':
          return {
            mockPageId: 'code.ftl',
            mockData: [
              {
                pageId: 'code.ftl',
                code: {
                  success: false,
                },
              },
            ],
          };
        default:
          return {
            mockPageId: (template + '.ftl') as KcContextBase['pageId'],
          };
      }
    case MODE_PRIMARY_THEME_BUILD:
    case MODE_SECONDARY_THEME_BUILD:
    default:
      return {};
  }
};

export default mockData;
