import React from 'react';
import { KcContextBase } from 'keycloakify';
import Template from '../../modules/Template';
import { ELinkTarget, Link, Text, TextVariants } from '@monetize-tech/ui-kit';
import useMessage from '../../hooks/message.hook';
import styles from '../../assets/styles/pages.module.scss';

type LoginIdpLinkEmailProps = { kcContext: KcContextBase.LoginIdpLinkEmail };

const LoginIdpLinkEmail: React.FC<LoginIdpLinkEmailProps> = ({ kcContext }: LoginIdpLinkEmailProps) => {
  const { idpDisplayName, brokerContext, realm, url } = kcContext;

  const { msg } = useMessage();

  const headerNode = <Text variant={TextVariants.head3}>{msg('emailLinkIdpTitle', [idpDisplayName])}</Text>;
  const formNode = (
    <div className={styles.wrapper}>
      <div className={styles.list}>
        <Text variant={TextVariants.body2}>
          {msg('emailLinkIdp1', [idpDisplayName, brokerContext.username, realm?.displayName || ''])}
        </Text>
        <Text variant={TextVariants.body2}>
          {msg('emailLinkIdp2') + ' '}
          <Link href={url.loginAction} target={ELinkTarget.self} underline={false}>
            {msg('doClickHere')}
          </Link>
          {' ' + msg('emailLinkIdp3')}
        </Text>
        <Text variant={TextVariants.body2}>
          {msg('emailLinkIdp4') + ' '}
          <Link href={url.loginAction} target={ELinkTarget.self} underline={false}>
            {msg('doClickHere')}
          </Link>
          {msg('emailLinkIdp5')}
        </Text>
      </div>
    </div>
  );

  return <Template {...{ kcContext }} headerNode={headerNode} formNode={formNode} />;
};

export default React.memo(LoginIdpLinkEmail);
