import React, { PropsWithChildren, useContext, useEffect, useState } from 'react';
import Template from '../../modules/Template';
import type { KcContextBase } from 'keycloakify';
import useMessage from '../../hooks/message.hook';
import { Button, ButtonTypes, ButtonVariant, ELinkTarget, Link, Text, TextVariants } from '@monetize-tech/ui-kit';
import ReactMarkdown from 'react-markdown';
import tos_fr_url from './tos_fr.md';
import tos_en_url from './tos_en.md';
import LangContext from '../../context/LangContext';
import styles from './Terms.module.scss';

const Terms = ({ kcContext }: { kcContext: KcContextBase.Terms }) => {
  const { url } = kcContext;
  const { currentLanguageTag } = useContext(LangContext);

  const { msg } = useMessage();
  const [termsText, setTermsText] = useState(msg('termsText'));
  useEffect(() => {
    fetch(
      (() => {
        switch (currentLanguageTag) {
          case 'fr':
            return tos_fr_url;
          default:
            return tos_en_url;
        }
      })(),
    )
      .then((response) => response.text())
      .then((rawMarkdown) => setTermsText(rawMarkdown));
  }, [currentLanguageTag]);

  const headerNode = <Text variant={TextVariants.head3}>{msg('termsTitle')}</Text>;
  const formNode = (
    <div className={styles.wrapper}>
      <div className={`${styles.list} ${styles.markdown}`}>
        <ReactMarkdown
          // eslint-disable-next-line @typescript-eslint/ban-ts-comment
          // @ts-ignore
          renderers={{
            link: (linkProps: PropsWithChildren<{ href: string }>) => (
              <Link href={linkProps.href} target={ELinkTarget.blank} underline={false}>
                {linkProps.children}
              </Link>
            ),
          }}
        >
          {termsText}
        </ReactMarkdown>
      </div>
      <form className='form-actions' action={url.loginAction} method='POST'>
        <div className={styles.list}>
          <Button text={msg('doAccept')} type={ButtonTypes.submit} fluid className={styles.button} name='accept' />
          <Button
            text={msg('doDecline')}
            type={ButtonTypes.submit}
            fluid
            variant={ButtonVariant.outlined}
            className={styles.button}
            name='cancel'
          />
        </div>
      </form>
      <div className='clearfix' />
    </div>
  );

  return <Template {...{ kcContext }} displayMessage={false} headerNode={headerNode} formNode={formNode} />;
};

export default React.memo(Terms);
