import React from 'react';
import { Route, Navigate, Routes, BrowserRouter } from 'react-router-dom';
import Main from './modules/Main';
import Page from './modules/Page';
import { MODE_DEMO, MODE_PRIMARY_THEME_BUILD, ModeTypes } from './App.constants';

const multiplePages = (
  <Routes>
    <Route path='/' element={<Main />} />
    <Route path='/error' element={<Page template='error' mode={MODE_DEMO} />} />
    <Route path='/info' element={<Page template='info' mode={MODE_DEMO} />} />
    <Route path='/login' element={<Page template='login' mode={MODE_DEMO} />} />
    <Route path='/login-config-totp' element={<Page template='login-config-totp' mode={MODE_DEMO} />} />
    <Route path='/login-config-totp-2' element={<Page template='login-config-totp-2' mode={MODE_DEMO} />} />
    <Route path='/login-idp-link-confirm' element={<Page template='login-idp-link-confirm' mode={MODE_DEMO} />} />
    <Route path='/login-otp' element={<Page template='login-otp' mode={MODE_DEMO} />} />
    <Route path='/login-reset-password' element={<Page template='login-reset-password' mode={MODE_DEMO} />} />
    <Route path='/login-update-profile' element={<Page template='login-update-profile' mode={MODE_DEMO} />} />
    <Route path='/login-verify-email' element={<Page template='login-verify-email' mode={MODE_DEMO} />} />
    <Route path='/register' element={<Page template='register' mode={MODE_DEMO} />} />
    <Route path='/register-user-profile' element={<Page template='register-user-profile' mode={MODE_DEMO} />} />
    <Route path='/saml-post-form' element={<Page template='saml-post-form' mode={MODE_DEMO} />} />
    <Route path='/login-update-password' element={<Page template='login-update-password' mode={MODE_DEMO} />} />
    <Route path='/terms' element={<Page template='terms' mode={MODE_DEMO} />} />
    <Route path='/code' element={<Page template='code' mode={MODE_DEMO} />} />
    <Route path='/code-2' element={<Page template='code-2' mode={MODE_DEMO} />} />
    <Route path='/delete-account-confirm' element={<Page template='delete-account-confirm' mode={MODE_DEMO} />} />
    <Route path='/login-idp-link-email' element={<Page template='login-idp-link-email' mode={MODE_DEMO} />} />
    <Route path='/login-oauth-grant' element={<Page template='login-oauth-grant' mode={MODE_DEMO} />} />
    <Route path='/login-page-expired' element={<Page template='login-page-expired' mode={MODE_DEMO} />} />
    <Route
      path='/login-oauth2-device-verify-user-code'
      element={<Page template='login-oauth2-device-verify-user-code' mode={MODE_DEMO} />}
    />
    <Route path='/login-password' element={<Page template='login-password' mode={MODE_DEMO} />} />
    <Route path='/login-username' element={<Page template='login-username' mode={MODE_DEMO} />} />
    <Route path='/select-authenticator' element={<Page template='select-authenticator' mode={MODE_DEMO} />} />
    <Route path='*' element={<Navigate to='/' />} />
  </Routes>
);

const useRoutes = (mode: ModeTypes | undefined) => {
  if (mode === MODE_DEMO) {
    return <BrowserRouter>{multiplePages}</BrowserRouter>;
  }

  return <Page mode={mode || MODE_PRIMARY_THEME_BUILD} />;
};

export default useRoutes;
