import React, { useCallback, useState } from 'react';
import Template from '../../modules/Template';
import type { KcContextBase } from 'keycloakify';
import {
  Button,
  ButtonTypes,
  Input,
  Text,
  TextVariants,
  Icon,
  IconSizes,
  TextAndIconColors,
} from '@monetize-tech/ui-kit';
import useMessage from '../../hooks/message.hook';
import styles from './LoginOtp.module.scss';
import classNames from 'classnames';
import { ReactComponent as MobileIcon } from '../../assets/icons/mobile.svg';

const LoginOtp = ({ kcContext }: { kcContext: KcContextBase.LoginOtp }) => {
  const { otpLogin, url } = kcContext;

  const { msg } = useMessage();

  const [otp, setOtp] = useState('');
  const changeOtp = useCallback((newValue: string) => setOtp(newValue), [setOtp]);
  const [selected, setSelected] = useState<string>(otpLogin.userOtpCredentials[0].id || '');

  const headerNode = <Text variant={TextVariants.head3}>{msg('doLogIn')}</Text>;
  const formNode = (
    <div className={styles.wrapper}>
      <form id='kc-otp-login-form' action={url.loginAction} method='post'>
        {otpLogin.userOtpCredentials.length > 1 && (
          <div className={classNames(styles.list, { [styles.row]: true })}>
            {otpLogin.userOtpCredentials.map((otpCredential) => (
              <div
                key={otpCredential.id}
                className={classNames(styles.card, { [styles.selected]: otpCredential.id === selected })}
                onClick={() => setSelected(otpCredential.id)}
              >
                <input
                  type='hidden'
                  value={otpCredential.id}
                  name={selected === otpCredential.id ? 'selectedCredentialId' : ''}
                />
                <Icon IconComponent={MobileIcon} color={TextAndIconColors.black} size={IconSizes.l} />
                <Text variant={TextVariants.body2}>{otpCredential.userLabel}</Text>
              </div>
            ))}
          </div>
        )}
        <div className={styles.list}>
          <Input name='otp' type='text' onChange={changeOtp} value={otp} placeholder={msg('loginOtpOneTime')} />
        </div>
        <div className={styles.list}>
          <Button text={msg('doLogIn')} type={ButtonTypes.submit} fluid className={styles.button} name='login' />
        </div>
      </form>
    </div>
  );

  return <Template {...{ kcContext }} headerNode={headerNode} formNode={formNode} />;
};

export default React.memo(LoginOtp);
