import React from 'react';
import useRoutes from './routes';
import { EThemeProviderThemes, ThemeProvider } from '@monetize-tech/ui-kit';
import { ModeTypes } from './App.constants';

const App: React.FC = () => {
  const routes = useRoutes(process.env.REACT_APP_MODE as ModeTypes);
  return (
    <ThemeProvider theme={EThemeProviderThemes.convertSocialLight} style={{ height: '100%' }}>
      {routes}
    </ThemeProvider>
  );
};

export default App;
