import React from 'react';
import Template from '../../modules/Template';
import type { KcContextBase } from 'keycloakify';
import useMessage from '../../hooks/message.hook';
import styles from '../../assets/styles/pages.module.scss';
import { Button, ButtonTypes, Text, TextVariants } from '@monetize-tech/ui-kit';

const LoginIdpLinkConfirm = ({ kcContext }: { kcContext: KcContextBase.LoginIdpLinkConfirm }) => {
  const { url } = kcContext;

  const { msg } = useMessage();

  const headerNode = <Text variant={TextVariants.head3}>{msg('confirmLinkIdpTitle')}</Text>;
  const formNode = (
    <div className={styles.wrapper}>
      <form id='kc-register-form' action={url.loginAction} method='post'>
        <div className={styles.list}>
          <Button
            text={msg('confirmLinkIdpReviewProfile')}
            type={ButtonTypes.submit}
            fluid
            className={styles.button}
            value='updateProfile'
            name='submitAction'
          />
          <Button
            text={msg('confirmLinkIdpContinue')}
            type={ButtonTypes.submit}
            fluid
            className={styles.button}
            name='submitAction'
            value='linkAccount'
          />
        </div>
      </form>
    </div>
  );

  return <Template {...{ kcContext }} headerNode={headerNode} formNode={formNode} />;
};

export default React.memo(LoginIdpLinkConfirm);
